import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, TabPanel, ToggleControl, TextControl, __experimentalUnitControl as UnitControl, __experimentalNumberControl as NumberControl, __experimentalBorderControl as BorderControl, __experimentalBoxControl as BoxControl } from '@wordpress/components';
import produce from 'immer';

// Custom Components 
import { colors, emUnit, perUnit, pxUnit, vhUnit, tabs } from './Const/options';
import MultiShadowControl from '../../Components/MultiShadowControl';
import ColorControl from '../../Components/ColorControl';
import { tabController } from './Const/functions';


const Settings = (props) => {
	const { attributes, setAttributes, updateTracks } = props;
	const { options, tracks, iframeStyle, frameBorder, frameBRadius, frameBoxShadow } = attributes;
	const { alignment, isAutoPlay, color, isBuying, isSharing, isDownload, isArtWork, isPlayCount, isUser, isSingleActive, isComments, isVisual, isTeaser, isHideRelated } = options;

	const { width, height } = iframeStyle;

	// attributes object property setAttributes function declare 
	const updateObject = (attr, key, val) => {
		const newAttr = { ...attributes[attr] }
		newAttr[key] = val;
		setAttributes({ [attr]: newAttr });
	}

	return <>
		<InspectorControls style={{ marginBottom: "40px" }}>
			<TabPanel className="bPlTabPanel scbTabPanel" activeClass="activeTab" onSelect={() => tabController()} tabs={tabs}>
				{(tab) => <>
					{'General' == tab.name && <>
						<PanelBody className='bPlPanelBody' title={__('Sound Cloud Settings', 'sound-cloud')} initialOpen={true}>

							{tracks.map((track, index) => <>
								{/* Url Field  */}
								<TextControl className='scbCustomDesign mb20' key={index} label={__("Url", 'sound-cloud')} labelPosition={__('top', 'sound-cloud')} value={track.url} placeholder={__('Enter Track Url', 'sound-cloud')} onChange={value => updateTracks('url', index, value)} />
								{/* Check tracks Set than start track  */}
								{track.url.includes("sets") && <>
									<NumberControl key={index} className='mb20' label={__('Start Track', 'sound-cloud')} labelPosition='left' value={track.startTrack}
										onChange={(value) => {
											updateTracks('startTrack', index, value)
										}} />
								</>
								}
								<div className="scbWHControl">
									{/* Width  */}
									<UnitControl className='mb20' label={__('Width', 'sound-cloud')} labelPosition="left"
										onChange={(value) => {
											updateObject('iframeStyle', 'width', value)
										}} value={width} units={[pxUnit(650), perUnit(100), emUnit(40)]} isResetValueOnUnitChange={true}
									/>

									{/* Height  */}
									<UnitControl className='mb20' label={__('Height', 'sound-cloud')} labelPosition="left"
										onChange={(value) => {
											updateObject('iframeStyle', 'height', value)
										}} value={height} units={[pxUnit(400), vhUnit(50)]} isResetValueOnUnitChange={true}
									/>
								</div>
							</>)}
						</PanelBody>


						<PanelBody className='bPlPanelBody' title={__('Options', 'sound-cloud')} initialOpen={false}>
							<ToggleControl label={__('Enable Auto Play ', 'sound-cloud')} className='mt20' checked={isAutoPlay} onChange={(value) => { updateObject('options', 'isAutoPlay', value); }} />

							<ToggleControl label={__('Single Active ', 'sound-cloud')} className='mt20' checked={isSingleActive} onChange={(value) => { updateObject('options', 'isSingleActive', value); }}
							/>

							<ToggleControl label={__('Visual Design', 'sound-cloud')} className='mt20' checked={isVisual} onChange={(value) => { updateObject('options', 'isVisual', value); }} />

							<ToggleControl label={__('Teaser ', 'sound-cloud')} className='mt20' checked={isTeaser}
								onChange={(value) => { updateObject('options', 'isTeaser', value); }} />
						</PanelBody>


						<PanelBody className='bPlPanelBody' title={__('Elements', 'sound-cloud')} initialOpen={false}>
							<ToggleControl label={__('Show Buying ', 'sound-cloud')} className='mt20' checked={isBuying} onChange={(value) => { updateObject('options', 'isBuying', value); }} />

							{!isBuying && <>
								<ToggleControl label={__('Show Download ', 'sound-cloud')} className='mt20' checked={isDownload} onChange={(value) => { updateObject('options', 'isDownload', value); }} />
							</>}

							<ToggleControl label={__('Comments ', 'sound-cloud')} className='mt20' checked={isComments} onChange={(value) => { updateObject('options', 'isComments', value); }}
							/>

							<ToggleControl label={__('Show Artwork ', 'sound-cloud')} className='mt20' checked={isArtWork} onChange={(value) => { updateObject('options', 'isArtWork', value); }} />

							<ToggleControl label={__('Show Play Count ', 'sound-cloud')} className='mt20' checked={isPlayCount} onChange={(value) => { updateObject('options', 'isPlayCount', value); }} />

							<ToggleControl label={__('Show User ', 'sound-cloud')} className='mt20' checked={isUser}
								onChange={(value) => { updateObject('options', 'isUser', value); }} />

							<ToggleControl label={__('Hide Related ', 'sound-cloud')} className='mt20' checked={isHideRelated} onChange={(value) => { updateObject('options', 'isHideRelated', value); }} />

							<ToggleControl label={__('Show Sharing ', 'sound-cloud')} className='mt20' checked={isSharing} onChange={(value) => { updateObject('options', 'isSharing', value); }} />
						</PanelBody>
					</>}

					{'style' == tab.name && <>
						<PanelBody className='bPlPanelBody scbPanelBody' title={__('Sound Cloud Style', 'sound-cloud')} initialOpen={true}>
							<ColorControl label={__('Color', 'sound-cloud')} value={color} onChange={(value) => updateObject('options', 'color', value)} defaultColor="#f50" disableAlpha={true} />

							<BorderControl className="mb20 mt10" label={__('Border', 'sound-cloud')} value={frameBorder} onChange={(value) => setAttributes({ frameBorder: value })} colors={colors} />

							<BoxControl className="mt20" label={__("Border Radius", 'sound-cloud')} values={frameBRadius}
								onChange={value => setAttributes({ frameBRadius: value })} units={[pxUnit(4), perUnit(30)]} resetValues={{ top: 0, bottom: 0, left: 0, right: 0 }}
							/>

							<MultiShadowControl label={__('Shadow:', 'sound-cloud')} value={frameBoxShadow?.shadow || frameBoxShadow} onChange={val => setAttributes({ frameBoxShadow: val })} produce={produce} />
						</PanelBody>
					</>}
				</>}
			</TabPanel>
		</InspectorControls>

		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Sound Cloud Alignment')} alignmentControls={[
				{ title: __('Sound Cloud in left', 'sound-cloud'), align: 'left', icon: 'align-left' },
				{ title: __('Sound Cloud in center', 'sound-cloud'), align: 'center', icon: 'align-center' },
				{ title: __('Sound Cloud in right', 'sound-cloud'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>
	</>
};
export default Settings;