
import { useEffect, useState } from '@wordpress/element';
import produce from 'immer';

// Settings Components
import { tabController } from './Const/functions';
import Settings from './Settings';
import Widget from './Widget';
import Style from './Style';

const Edit = props => {

	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { tracks } = attributes;
	const [activeIndex, setActiveIndex] = useState(0);
	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	/* 
	* 
	* Tracks Update 
	*
	*/
	const updateTracks = (type, index, val, childType = false) => {
		const newTracks = produce(tracks, draft => {
			if (childType) {
				draft[index][type][childType] = val;
			} else {
				draft[index][type] = val;
			}
		});
		setAttributes({ tracks: newTracks });
	}

	return <>

		<Settings attributes={attributes} setAttributes={setAttributes} updateTracks={updateTracks} />

		<div className={className} id={`scbSoundCloud-${clientId}`}>

			{!isSelected && <div style={{ width: '100%', height: '100%', position: 'absolute' }}></div>}

			<Style attributes={attributes} clientId={clientId} />

			<Widget attributes={attributes} clientId={clientId} isBackend={true} custom={{ activeIndex, setActiveIndex }} />

		</div>
	</>;
};
export default Edit;